import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import { postContactsOperation } from "../../redux/contacts/contacts-operation";
import { getContacts } from "../../redux/contacts/contacts-selectors";
import s from "./ContactForm.module.css";

const ContactForm = () => {
  const [name, setName] = useState("");
  const [number, setNumber] = useState("");

  const contacts = useSelector(getContacts);

  const dispatch = useDispatch();

  const onChange = (e) => {
    const { name, value } = e.currentTarget;
    switch (name) {
      case "name":
        setName(value);
        break;

      case "number":
        setNumber(value);
        break;

      default:
        return;
    }
  };

  const onSubmit = (e) => {
    e.preventDefault();

    const contactCheck = contacts.find(
      (contact) => contact.name.toLowerCase() === name.toLowerCase()
    );

    if (contactCheck) {
      setName("");
      setNumber("");
      alert(`${name} is already in contact`);
      return;
    }
    dispatch(postContactsOperation({ name, number }));
    setName("");
    setNumber("");
  };

  return (
    <form className={s.form} onSubmit={onSubmit}>
      <label className={s.label}>
        Name
        <input
          className={s.input}
          onChange={onChange}
          type="text"
          value={name}
          name="name"
          pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
          title="Ð˜Ð¼Ñ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¸Ð· Ð±ÑƒÐºÐ², Ð°Ð¿Ð¾ÑÑ‚Ñ€Ð¾Ñ„Ð°, Ñ‚Ð¸Ñ€Ðµ Ð¸ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ‚. Ð¿."
          required
        />
      </label>
      <label className={s.label}>
        Number
        <input
          className={s.input}
          onChange={onChange}
          type="tel"
          name="number"
          pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
          title="ÐÐ¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ†Ð¸Ñ„Ñ€ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ñ‹, Ñ‚Ð¸Ñ€Ðµ, ÐºÑ€ÑƒÐ³Ð»Ñ‹Ðµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ Ð½Ð°Ñ‡Ð¸Ð½Ð°Ñ‚ÑŒÑÑ Ñ +"
          value={number}
          required
        />
      </label>
      <button className={s.button} type="submit">
        Add contact
      </button>
    </form>
  );
};

export default ContactForm;
